import { loadImage } from 'sol-common';
import Snap from 'sol-common/snap';
import { alignGroup, SOL } from 'sol-common/util';
//background
import BG_DAY from '../assets/images/P004/BG_DAY.svg';
import BG_NIGHT from '../assets/images/P004/BG_NIGHT.svg';
import tinting from '../assets/images/P004/tinting.svg';
import rearViewMirror from '../assets/images/P004/mirror.svg';
import RoadLine1 from '../assets/images/P004/RoadLine1.svg';
import RoadLine2 from '../assets/images/P004/RoadLine2.svg';
import RoadLine3 from '../assets/images/P004/RoadLine3.svg';
import gas_station from '../assets/images/P004/gas_station.svg';
import steering_wheel from '../assets/images/P004/steering_wheel.svg';
//choice
import left_sign from '../assets/images/P004/left_sign.svg';
import right_sign from '../assets/images/P004/right_sign.svg';
//fireworks
import fireworks1 from '../assets/images/P004/fireworks1.svg';
import fireworks2 from '../assets/images/P004/fireworks2.svg';
import fireworks3 from '../assets/images/P004/fireworks3.svg';
import fireworks4 from '../assets/images/P004/fireworks4.svg';
//OPED
import meter from '../assets/images/P004/meter_G.svg';
import needle from '../assets/images/P004/needle.svg';
import oil from '../assets/images/P004/oil_G.svg';

import { D1Generate } from '../utils/D1Generate';
import D1feedback from '../components/D1feedback';
import D1directive from '../components/D1directive';
import { getPointInScatterForm, getRandomArrayElement, getRandomNumber, wait } from '../utils/util';
import D1Animate from '../utils/D1Animate';

export default function DM000005({ canvas = SOL.SVG, ...p }) {
    const g = canvas;
    const callback = p.callback;

    const backgroundG = g.g().addClass('D1-background');
    const choiceG = g.g().addClass('D1-choice');
    const choiceLeftG = choiceG.g().addClass('cp D1-choie-item-left').attr({ pointerEvents: 'none' });
    const choiceRightG = choiceG.g().addClass('cp D1-choie-item-right').attr({ pointerEvents: 'none' });
    const choiceBackG = g.g().addClass('D1-choice-back');
    const openingG = g.g().addClass('D1-opening');

    const D1generate = new D1Generate(p.generate);    

    let roadAnim;
    let dayAnim;
    let choiceAnim;
    let fireworksAnim;

    const drawBackground = () => {
        const { dayTime: fadeDuration, roadIntervalTime = 450 } = p.background;

        // 배경 이미지 로드
        const dayBgLoadImage = loadImage({ src: BG_DAY, key: 'P004-BG-DAY', type: 'svg' });
        const nightBgLoadImage = loadImage({ src: BG_NIGHT, key: 'P004-BG-NIGHT', type: 'svg' });
        const RoadLine1LoadImage = loadImage({ src: RoadLine1, key: 'P004-road-line1', type: 'svg' });
        const RoadLine2LoadImage = loadImage({ src: RoadLine2, key: 'P004-road-line2', type: 'svg' });
        const RoadLine3LoadImage = loadImage({ src: RoadLine3, key: 'P004-road-line3', type: 'svg' });

        // 배경 이지미 그리기
        const nightImage = nightBgLoadImage.use().transform('t-80, 0').appendTo(backgroundG);
        const dayImage = dayBgLoadImage.use().transform('t-80, 0').appendTo(backgroundG);
        const RoadLine1Image = RoadLine1LoadImage.use().transform('t-21, 395').appendTo(backgroundG);
        const RoadLine2Image = RoadLine2LoadImage.use().transform('t-21, 395').appendTo(backgroundG).attr({ opacity: '0' });
        const RoadLine3Image = RoadLine3LoadImage.use().transform('t-21, 395').appendTo(backgroundG).attr({ opacity: '0' });

        // 차선 및 일 애니메이션 생성
        roadAnim = drawRoadAnimation();
        dayAnim = drawDayAnimation();

        roadAnim.start();
        dayAnim.start();

        // 함수 정의
        function drawRoadAnimation() {
            let _pause = false;
            let interval;
            let n = 0;
            const pause = () => (_pause = true);
            const resume = () => (_pause = false);
            const remove = () => clearInterval(interval);
            const start = () => {
                interval = setInterval(() => {
                    if (_pause) return;
                    RoadLine2Image.attr({ opacity: Number(n % 3 === 2) });
                    RoadLine1Image.attr({ opacity: Number(n % 3 === 1) });
                    RoadLine3Image.attr({ opacity: Number(n % 3 === 0) });
                    n += 1;
                }, roadIntervalTime);
            };

            return { start, pause, resume, remove };
        }

        function drawDayAnimation() {
            let day, night;

            const fadeAnim = ({ el, type, duration }) => {
                const isIn = type === 'in';
                return Snap.animate(0, 1, val => el.attr({ opacity: isIn ? val : 1 - val }), duration, undefined, undefined);
            };
            const start = () => {
                day = fadeAnim({ el: dayImage, type: 'out', duration: fadeDuration });
                night = fadeAnim({ el: nightImage, type: 'in', duration: fadeDuration });
            };
            const pause = () => {
                day.pause();
                night.pause();
            };
            const resume = () => {
                day.resume();
                night.resume();
            };

            return { start, pause, resume };
        }
    };

    const drawChoiceItem = () => {
        const { repeatCount } = p.choice;
        const duration = 3000;
        const stopDuration = 1200;
        const gasStationStopDuration = 1500;
        const handleDuration = duration - stopDuration;

        const isAnimEnd = { left: false, right: false };
        const handleState = {
            position: 'origin',
            dx: 45,
            rotate: 20
        };

        let n = 0;
        // 이미지 로드
        const leftSignLoadImage = loadImage({ src: left_sign, key: 'P004-left-sign', type: 'svg' });
        const rightSignLoadImage = loadImage({ src: right_sign, key: 'P004-right-Sign', type: 'svg' });
        const tintingLoadImage = loadImage({ src: tinting, key: 'P004-tinting', type: 'svg' });
        const rearViewMirrorLoadImage = loadImage({ src: rearViewMirror, key: 'P004-rear-view-mirror', type: 'svg' });
        const steeringWheelLoadImage = loadImage({ src: steering_wheel, key: 'P004-steering-wheel', type: 'svg' });
        const gasStationLoadImage = loadImage({ src: gas_station, key: 'P004-gas-station', type: 'svg' });

        // 이미지 그리기
        const size = { left: leftSignLoadImage.getBBox(), right: rightSignLoadImage.getBBox() };
        leftSignLoadImage
            .use()
            .transform('t' + [-size.left.w / 2 - 30, -size.left.h])
            .appendTo(choiceLeftG);
        rightSignLoadImage
            .use()
            .transform('t' + [-size.left.w / 2 + 30, -size.left.h])
            .appendTo(choiceRightG);
        tintingLoadImage.use().appendTo(choiceBackG);
        rearViewMirrorLoadImage
            .use()
            .transform('t' + [(800 - rearViewMirrorLoadImage.getBBox().w) / 2, 0])
            .appendTo(choiceBackG);
        steeringWheelLoadImage.use().transform('t282, 360 s0.7').appendTo(choiceBackG);

        // 초이스 애니메이션 생성
        choiceAnim = drawChoiceAnim({ animEnd: where => animEndHandler(where) });

        // 초이스 클릭 이벤트 생성
        choiceLeftG.click(() => choiceHandler(choiceLeftG));
        choiceRightG.click(() => choiceHandler(choiceRightG));

        // 시작
        const start = () => {
            setChoiceData();
            choiceAnim.start();
            setTimeout(() => {
                setChoicePointer('auto');
                choiceAnim.pause();
                roadAnim.pause();
            }, stopDuration);
        };

        // 함수 정의
        function setChoicePointer(state) {
            choiceLeftG.attr({ pointerEvents: state });
            choiceRightG.attr({ pointerEvents: state });
        }

        function setChoiceData() {
            let number;
            do {
                D1generate.gen();
                number = D1generate.num;
            } while (number[0] === number[1]);

            const answer = number[0] > number[1];
            // 기존 텍스트 삭제
            if (choiceLeftG.select('text')) choiceLeftG.select('text').remove();
            if (choiceRightG.select('text')) choiceRightG.select('text').remove();

            // 텍스트 그리기
            choiceLeftG
                .text(0, -size.left.h + 60, number[0])
                .addClass('dn53')
                .attr({ fontSize: 40 })
                .anchor('right');

            choiceRightG
                .text(0, -size.left.h + 60, number[1])
                .addClass('dn53')
                .attr({ fontSize: 40 });
            // 초이스 데이터 설정
            choiceLeftG.data('data', { num: number[0], answer, from: 'left' });
            choiceRightG.data('data', { num: number[1], answer: !answer, from: 'right' });

            return;
        }

        async function choiceHandler(self) {
            setChoicePointer('none');
            const { answer, from } = self.data('data');
            const sign = from === 'left' ? -1 : 1;
            const weight = handleState.position === 'origin' ? 1 : 2;

            if (answer) await D1feedback({ el: self, bool: true });
            choiceAnim.resume();
            roadAnim.resume();
            if (handleState.position !== from) {
                await handleAnim({ duration: handleDuration - 150, dx: sign * weight * handleState.dx, rotate: sign * handleState.rotate });
                handleState.position = from;
                await wait(200);
                handleState.position = 'origin';
            }

            return;
        }

        function animEndHandler(from) {
            isAnimEnd[from] = true;
            if (isAnimEnd.left && isAnimEnd.right) {
                n += 1;
                if (n < repeatCount) {
                    setChoiceData();
                    choiceAnim.start();
                    setTimeout(() => {
                        setChoicePointer('auto');
                        choiceAnim.pause();
                        roadAnim.pause();
                    }, stopDuration);

                    isAnimEnd.left = false;
                    isAnimEnd.right = false;
                }
                if (n === repeatCount) {
                    choiceAnim.last();
                    setTimeout(async () => {
                        choiceAnim.pause();
                        roadAnim.remove();

                        await wait(300);
                        choiceBackG.remove();                        

                        await fireworksAnim.start();
                        await wait(300);
                        callback();
                    }, gasStationStopDuration);
                }
            }
        }

        function drawChoiceAnim({ animEnd: endCallback }) {
            const startPt = { x: 120, y: 395 };
            const gap = { x: 430, y: 115 };
            const leftPath = `M${400 - startPt.x},${startPt.y} l-${gap.x}, ${gap.y}`;
            const rightPath = `M${400 + startPt.x},${startPt.y} l${gap.x}, ${gap.y}`;
            const easing = val => Math.pow(val, 1.5);

            let leftAnim;
            let rightAnim;

            // g.path(leftPath).attr({ stroke: 'grey' });
            // g.path(rightPath).attr({ stroke: 'grey' });

            const start = () => {
                leftAnim = choiceMoveAnim({ path: leftPath, duration, element: choiceLeftG, from: 0.05, to: 2.4, type: 'left', easing, endCallback });
                rightAnim = choiceMoveAnim({ path: rightPath, duration, element: choiceRightG, from: 0.05, to: 2.4, type: 'right', easing, endCallback });

                leftAnim.start();
                rightAnim.start();
            };
            const pause = () => {
                leftAnim.pause();
                rightAnim.pause();
            };
            const resume = () => {
                leftAnim.resume();
                rightAnim.resume();
            };

            const last = () => {
                choiceRightG.clear();
                const { w, h } = gasStationLoadImage.getBBox();
                gasStationLoadImage
                    .use()
                    .transform('t' + [-w / 2 + 30, -h - 15])
                    .appendTo(choiceRightG);
                rightAnim = choiceMoveAnim({
                    path: rightPath,
                    duration,
                    element: choiceRightG,
                    from: 0.05,
                    to: 2.4,
                    type: 'right',
                    easing
                });

                rightAnim.start();
            };

            return { start, pause, resume, last };
        }

        function handleAnim({ duration, dx, rotate: _rotate }) {
            const { local } = steeringWheelLoadImage.transform();
            const breakInterval = 0.4;
            return new Promise(resolve =>
                Snap.animate(
                    0,
                    1,
                    val => {
                        // const translate = 't' + [val * dx, 0];
                        if (val < breakInterval) val = (1 / breakInterval) * val;
                        else val = (1 / (breakInterval - 1)) * val + 1 / (1 - breakInterval);
                        const rotate = 'r' + val * _rotate;
                        const translate = 't' + val * dx * -1;
                        steeringWheelLoadImage.transform(local + rotate);
                        backgroundG.transform(translate);
                        choiceG.transform(translate);
                    },
                    duration,
                    undefined,
                    resolve
                )
            );
        }

        function choiceMoveAnim({ path, duration, element, from, to, type, easing, endCallback }) {
            let anim;
            const startPt = Snap.path.getPointAtLength(path, 0);
            element.attr({ transformOrigin: 'right bottom', transform: `translate(${startPt.x}, ${startPt.y}) scale(${from})` });
            const start = () => {
                anim = Snap.animate(
                    0,
                    1,
                    val => {
                        const { x, y } = Snap.path.getPointAtLength(path, val * Snap.path.getTotalLength(path));
                        // if (val < 0.8) val = val / 0.8;
                        // else val = 1;
                        const scale = val * (to - from) + from;
                        element.attr({ transform: `translate(${x}, ${y}) scale(${scale})` });
                    },
                    duration,
                    easing,
                    () => {
                        endCallback(type);
                        anim.stop();
                    }
                );
            };
            const pause = () => anim.pause();
            const resume = () => anim.resume();
            return { start, pause, resume };
        }

        return { start };
    };

    const drawFireWorks = () => {
        const { num } = p.fireworks;
        const fireworkdG = backgroundG.g().addClass('D1-fireworks');
        const opacityBreak = 3.5;
        // 이미지 로드
        const fireworks1LoadImage = loadImage({ src: fireworks1, key: 'P004-fireworks-1', type: 'svg' });
        const fireworks2LoadImage = loadImage({ src: fireworks2, key: 'P004-fireworks-2', type: 'svg' });
        const fireworks3LoadImage = loadImage({ src: fireworks3, key: 'P004-fireworks-3', type: 'svg' });
        const fireworks4LoadImage = loadImage({ src: fireworks4, key: 'P004-fireworks-4', type: 'svg' });
        const loadImages = [fireworks1LoadImage, fireworks2LoadImage, fireworks3LoadImage, fireworks4LoadImage];
        const randomLoadImages = getRandomArrayElement(loadImages, num);
        // 이미지 그리기
        const points = getPointInScatterForm({ area: { x: 50, y: 25, width: 500, height: 125 }, pointNum: num, neighborhood: { width: 50, height: 50 } });

        const images = points.map((el, i) =>
            randomLoadImages[i]
                .use()
                .transform('t' + [el.cx, el.cy])
                .appendTo(fireworkdG)
                .attr({ opacity: 0 })
        );

        fireworksAnim = drawfireworksAnim();

        function drawfireworksAnim() {
            const fireAnim = ({ element }) =>
                new Promise(resolve => {
                    const { local } = element.transform();
                    const scaleWeight = getRandomNumber(1.1, 1.2);
                    const timeOut = getRandomNumber(0, 300);
                    const duration = getRandomNumber(750, 1500);
                    setTimeout(() => {
                        Snap.animate(
                            0,
                            1,
                            val => {
                                element.transform(local + ('s' + val * scaleWeight)).attr({ opacity: -opacityBreak * (val - 1) });
                            },
                            duration,
                            undefined,
                            resolve
                        );
                    }, timeOut);
                });
            const fireworksanims = images.map(el => fireAnim.bind(null, { element: el }));

            const start = async () => {
                await Promise.all(fireworksanims.map(el => el()));
            };

            return { start };
        }
    };

    const drawDirectiveText = () => {
        const { text, fontSize, ttsOffet, center } = p.directive;
        const { directive } = D1directive({ g: choiceBackG, text, fontSize, ttsOffet });
        alignGroup({ g: directive, x: center.x, y: center.y });
    };

    const drawOpening = () => {
        // 이미지 로드
        const meterLoadImage = loadImage({ src: meter, key: 'P004-meter', type: 'svg' });
        const needleLoadImage = loadImage({ src: needle, key: 'P004-needle', type: 'svg' });
        const oilLoadImage = loadImage({ src: oil, key: 'P004-oil', type: 'svg' });

        const meterBBox = meterLoadImage.getBBox();
        const meterTranslate = [(800 - meterBBox.w) / 2, 150];
        const needleImageImagePoint = { x: 380, y: 264 };
        let meterImage, emptyOilImage, fullOilImage, needleImage;
        let fullOilMaskRect, emptyOilMaskRect;

        const drawBackground = () => openingG.rect(0, 0, 800, 500).attr({ opacity: 0.3 });
        const drawText = () => {
            openingG
                .text(meterTranslate[0] - 5, meterTranslate[1] + meterBBox.h + 40, 'E')
                .addClass('ffgt')
                .attr({ fill: '#ffffff', fontSize: 40 });
            openingG
                .text(meterTranslate[0] + meterBBox.w - 15, meterTranslate[1] + meterBBox.h + 40, 'F')
                .addClass('ffgt')
                .attr({ fill: '#ffffff', fontSize: 40 });
        };
        const drawImages = () => {
            meterImage = meterLoadImage
                .use()
                .transform('t' + meterTranslate)
                .appendTo(openingG);
            emptyOilImage = oilLoadImage
                .use()
                .transform('t' + [367.5, 220])
                .attr({ opacity: 0.5 })
                .appendTo(openingG);
            fullOilImage = oilLoadImage
                .use()
                .transform('t' + [367.5, 220])
                .attr({ opacity: 1 })
                .appendTo(openingG);
            needleImage = needleLoadImage
                .use()
                .attr({
                    x: needleImageImagePoint.x,
                    y: needleImageImagePoint.y
                })
                .appendTo(openingG);
        };
        const setMask = () => {
            const emptyOilMask = openingG.mask();
            const fullOilMask = openingG.mask();
            const emptyOilG = openingG.g().addClass('D1-empty-oil');
            const fullOilG = openingG.g().addClass('D1-full-oil');
            const { x, y, w, h } = emptyOilImage.getBBox();

            fullOilMaskRect = fullOilMask.rect(x, y, w, h).attr({ fill: 'white' });
            emptyOilMaskRect = emptyOilMask.rect(x, y - h, w, h).attr({ fill: 'white' });

            emptyOilImage.appendTo(emptyOilG);
            fullOilImage.appendTo(fullOilG);

            fullOilG.attr({ mask: fullOilMask });
            emptyOilG.attr({ mask: emptyOilMask });

            needleImage.appendTo(openingG);
        };
        const start = () => {
            const { w, h } = needleLoadImage.getBBox();

            return new Promise(resolve => {
                Snap.animate(
                    0,
                    1,
                    val => {
                        fullOilMaskRect.transform('t' + [0, val * emptyOilImage.getBBox().h]);
                        emptyOilMaskRect.transform('t' + [0, val * emptyOilImage.getBBox().h]);
                        needleImage.attr({
                            transform: 'rotate(' + val * 126 * -1 + 'deg)',
                            'transform-origin': `${needleImageImagePoint.x - w / 2 + 20} ${needleImageImagePoint.y + h / 2 - 20}`
                        });
                    },
                    2000,
                    undefined,
                    async () => {
                        roadAnim.pause();
                        oilLoadImage.select('path').attr({ fill: '#fea028' });
                        meterLoadImage.selectAll('path').forEach(el => el.attr({ fill: '#fea028' }));
                        await wait(500);
                        await D1Animate.blink({ interval: 0, duration: 750, element: emptyOilImage, opacity: 0.5 });                        
                        await D1Animate.blink({ interval: 0, duration: 750, element: emptyOilImage, opacity: 0.5 });                        
                        resolve();                        
                    }
                );
            });
        };
        const remove = () => openingG.remove();

        drawBackground();
        drawText();
        drawImages();
        setMask();

        return { start, remove };
    };

    const start = async () => {        
        const opening = drawOpening();
        drawBackground();
        const choice = drawChoiceItem();
        drawDirectiveText();
        drawFireWorks();
        await wait(300);
        await opening.start();
        await wait(500);
        opening.remove();        
        roadAnim.resume();
        choice.start();
    };

    start();
}
