import D1tts from '../assets/images/D1tts.svg';
import { tts } from 'sol-common';

export default function D1directive({ g, text = '', x = 0, y = 0, ttsOffet = { x: 0, y: 0 }, fontSize = 25 }) {
    const directiveG = g.g();
    const directText = directiveG.g();

    const directive = directText.text(x, y, text).addClass('ffgt').attr({ fontSize });
    const bbox = directive.getTBox();
    const directiveTTS = tts({
        canvas: directiveG,
        x: bbox.x - ttsOffet.x - 45,
        y: bbox.y - ttsOffet.y,
        blinkOffset: { x: 0, y: 5 },
        text: text.replace(/\/n/g, ''),

        img: { src: D1tts, key: 'D1-tts', type: 'svg' }
    });

    return { directive: directiveG, text: directText, tts: directiveTTS };
}
